<script>
  import CountryCard from "./../containers/CountryCard.svelte";
  import Card from "./../containers/Card.svelte";
  import { onMount } from "svelte";
  import axios from "axios";

  let listOfCountries = ["AF", "DZ", "AO", "AR", "AM", "AU", "AT", "AZ"];

  let worldData;
  onMount(() => {
    axios
      .get("https://api.thevirustracker.com/free-api?global=stats")
      .then(res => {
        worldData = res.data;
      });
  });
</script>

<style lang="scss">.cards-container {
  display: flex;
  margin-bottom: 10rem; }

.countries-container {
  display: flex;
  flex-wrap: wrap;
  justify-self: center; }
/*# sourceMappingURL=src/pages/index.svelte.map */</style>

{#if worldData}
  <div class="cards-container">
    <Card
      color="#b3b300"
      title="Total sick worldwide"
      image={'data:image/jpeg;base64,'}
      number={worldData.results[0].total_cases} />
    <Card
      color="red"
      title="Total deaths worldwide"
      image={'data:image/png;base64,'}
      number={worldData.results[0].total_deaths} />
    <Card
      color="#39e600"
      title="Total recovered worldwide"
      image={'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcR7RGpCvhb33oMJ0K_I76tOHyB3PiMYGSV9i8TZqXOE9MmHsAEV&usqp=CAU'}
      number={worldData.results[0].total_recovered} />
  </div>
  <h1>Stats per country</h1>
  <div class="countries-container">
    {#each listOfCountries as code, i (i)}
      <CountryCard countryCode={code} />
    {/each}
  </div>
{/if}
